html, body {
  overflow-x: hidden;
}

body {
  font-size: 100%;
  position: relative;
  max-width: 100%;
  background: $white;
}

a img {
  border: none;
  outline: none;
}

.clearfix::before, .clearfix::after {
  display: table;
  content: "";
  clear: both;
}

.row--mob {
  width: 100%;

  @include breakpoint(tabletOnly) {
    float: left;
    width: 49.075%;
    margin-right: 1.85%;
    margin-bottom: 2.6vw;
  }

  @include breakpoint(desktopOnly) {
    margin-bottom: 4.6vw;
  }

  &:nth-child(2n+0) {
    margin-left: 0;
    margin-right: 0;
  }
}

.row--full {
  width: 100%;
}

.container--full {
  width: 100%;
  position: relative;
}

.container--padded {
  padding: 40px 0;
}

.container--raised {
  margin-top: -3.5%;
}

.container, .row--mob {
  position: relative;
}

.background--lightGray {
  background: $lightGray;
}

.background--yellow {
  background: $yellow;
}

@media only screen and (max-width: $break-mob) {

  .container {
    width: 87.5%;
    margin: 0 auto;
  }

}

@media only screen and (min-width: $break-mob) {

  .container {
    width: 100%;
  }

  .row--raised {
    margin-top: -10vw;
  }

  .row--mob.row--clipped--left {
    width: 44%;
  }

  .row--mob.row--clipped--right {
    width: 100%;

    @include breakpoint(tabletOnly) {
      width: 42.593%;
    }

    @include breakpoint(desktopOnly) {
      width: 42.784%;
    }
  }

  .row--clipped--left {
    margin-left: 5%;
  }

  .row--mob.row--centered {
    margin: 0 auto;
    float: none;
  }

}

@media only screen and (min-width: $break-desk) {

  .slider__container .slider {
    width: 90%;
  }

  .container--padded {
    padding: 60px 0;
  }

  .row--raised {
    margin-top: -8vw;
  }

  .col--half {
    width: 46%;
    padding: 10px 2%;
  }

}
